@charset "UTF-8";
/* light 300 */
@font-face {
  font-family: "Circle";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/circe_300.woff2") format("woff2"), url("../fonts/circe_300.woff") format("woff"); }

/* semi bold 600 */
@font-face {
  font-family: "Circle";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/circe_600.woff2") format("woff2"), url("../fonts/circe_600.woff") format("woff"); }

/* bold 700 */
@font-face {
  font-family: "Circle";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/circe_700.woff2") format("woff2"), url("../fonts/circe_700.woff") format("woff"); }

/* @media */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: "Circle", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 300;
  line-height: 24px;
  color: #131313;
  background-color: #ffffff;
  margin: 0;
  min-width: 320px; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  padding: 0 15px; }

@media (min-width: 768px) {
  .container {
    padding: 0 30px; } }

@media (min-width: 1000px) {
  .container {
    padding: 0 25px; } }

@media (min-width: 1370px) {
  .container {
    max-width: 1370px;
    padding: 0 110px;
    margin: 0 auto; } }

.product {
  padding-bottom: 50px; }

.product__image {
  display: block;
  margin: 0 auto; }

.product__list {
  display: none; }

.product__text {
  margin: 0;
  line-height: 18px; }

.product__about {
  margin-bottom: 20px; }

.product__title {
  display: none; }

.product__scroll {
  display: none; }

@media (min-width: 768px) {
  .product {
    display: flex;
    padding-top: 30px;
    padding-bottom: 0; }
  .product__visual {
    display: flex;
    justify-content: flex-start;
    width: 53.5%; }
  .product__image {
    margin: 0;
    width: 290px;
    height: 420px; }
  .product__items {
    order: -1;
    display: flex;
    flex-direction: column;
    align-items: center; }
  .product__list {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style: none; }
  .product__item {
    margin-bottom: 20px; }
  .product__scroll {
    display: flex;
    width: 40px;
    height: 30px;
    border: 1px solid #ebebeb;
    border-radius: 2px;
    background-color: transparent;
    position: relative; }
    .product__scroll::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 14px;
      width: 11px;
      height: 7px;
      background-image: url("../img/icons/check.png"); }
  .product__content {
    width: 41%; }
  .product__title {
    display: block;
    margin: 0;
    margin-bottom: 10px;
    font-size: 24px;
    text-transform: uppercase;
    font-weight: 700; }
  .product__group-content {
    display: flex;
    flex-direction: column; }
  .product__about {
    order: 0; }
  .product__text {
    font-size: 13px;
    padding-right: 7px; } }

@media (min-width: 1000px) {
  .product {
    justify-content: space-between;
    padding-bottom: 30px; }
  .product__visual {
    width: 49.1%; }
  .product__image {
    width: 340px;
    height: 492px; }
  .product__items {
    margin-right: 30px; }
  .product__item {
    margin-bottom: 25px; }
  .product__image-small {
    width: 90px;
    height: 130px; }
  .product__content {
    width: 44.3%;
    margin-top: 35px; }
  .product__about {
    margin-bottom: 30px; }
  .product__title {
    margin-top: 20px; } }

@media (min-width: 1370px) {
  .product__visual {
    width: 47%; }
  .product__content {
    width: 43.5%; }
  .product__items {
    margin-right: 110px; } }

.breadscrumbs {
  margin-bottom: 10px; }

.breadscrumbs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  max-width: 380px; }

.breadscrumbs__item {
  padding-right: 20px;
  position: relative; }
  .breadscrumbs__item::after {
    content: '';
    position: absolute;
    background-image: url("../img/icons/arrow.png");
    width: 11px;
    height: 5px;
    transform: translateY(-50%);
    top: 50%;
    right: 0; }
  .breadscrumbs__item:first-child {
    padding-left: 0; }
  .breadscrumbs__item::nth-child(3) {
    right: 20px; }
  .breadscrumbs__item:last-child {
    position: static;
    padding-right: 0;
    padding-left: 0; }

.breadscrumbs__link {
  display: block;
  font-size: 13px;
  color: #a2a3a3;
  font-weight: 300; }

@media (min-width: 768px) {
  .breadscrumbs__list {
    justify-content: flex-start;
    flex-wrap: wrap; }
  .breadscrumbs__item {
    display: flex;
    padding-right: 25px;
    margin-right: 12px; }
  .breadscrumbs__link {
    line-height: 16px;
    letter-spacing: 0.1px; } }

@media (min-width: 1000px) {
  .breadscrumbs {
    display: none; } }

.tag-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px; }

.tag-list__item {
  margin-right: 30px; }

.tag-list__link {
  color: #006be5; }

@media (min-width: 768px) {
  .tag-list__link {
    font-size: 13px; } }

@media (min-width: 1000px) {
  .tag-list {
    margin-bottom: 40px; } }

.price {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px; }

.price__text {
  margin: 0;
  font-size: 18px;
  line-height: 30px; }
  .price__text--promo {
    font-size: 15px; }

.price__text-replace {
  padding-right: 80px;
  color: #bfbebe;
  position: relative; }
  .price__text-replace::after {
    content: '2 499 ₽';
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    color: #9a9b9b;
    font-weight: 600; }

.price__text-new {
  font-size: 18px;
  line-height: 30px; }

@media (min-width: 1000px) {
  .price {
    flex-direction: row;
    justify-content: space-between;
    width: 82%;
    max-width: 380px; }
  .price__text:first-child {
    width: 41%; }
  .price__text:last-child {
    width: 55%;
    text-align: right; } }

@media (min-width: 1370px) {
  .price {
    width: 68%; } }

.available {
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: 20px;
  background-color: #f4f4f4; }

.available__text {
  margin: 0;
  padding-left: 45px;
  position: relative; }
  .available__text::before {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 15px; }
  .available__text--shop::before {
    background-image: url("../img/icons/tick.png");
    width: 14px;
    height: 10px; }
  .available__text--delivery::before {
    background-image: url("../img/icons/truck.png");
    width: 17px;
    height: 13px; }

.available__link {
  color: #006be5; }

@media (min-width: 768px) {
  .available {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 50px; }
  .available__text {
    font-size: 13px;
    line-height: 30px; } }

@media (min-width: 1000px) {
  .available {
    width: 82%;
    max-width: 380px; } }

@media (min-width: 1370px) {
  .available {
    width: 68%; } }

.more-details {
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: 20px;
  border-top: 1px solid #eeefef;
  border-bottom: 1px solid #eeefef; }

.more-details__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.more-details__drop {
  width: 100%;
  padding: 0;
  line-height: 41px;
  background-color: transparent;
  border: none;
  color: #1a1a1a;
  text-transform: uppercase;
  position: relative; }
  .more-details__drop::after {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 20px;
    width: 11px;
    height: 7px;
    background-image: url("../img/icons/arrow down.png"); }

@media (min-width: 768px) {
  .more-details {
    display: none; } }

.controls__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.controls__item {
  display: flex; }
  .controls__item--shop {
    width: 100%; }
  .controls__item--favorite {
    width: 82%;
    margin-right: auto; }
  .controls__item--share {
    width: 20px; }

.controls__link {
  background-color: #006be5;
  text-align: center;
  margin-bottom: 20px; }

.controls__link,
.controls__button {
  width: 100%;
  font-weight: 600;
  text-transform: uppercase;
  color: #fff;
  padding: 5px 0; }

.controls__button {
  color: #1a1a1a;
  background-color: transparent;
  border: 2px solid #d8d8d8; }
  .controls__button--share {
    padding: 0;
    border: none;
    position: relative; }
    .controls__button--share::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 18px;
      height: 24px;
      background-image: url("../img/icons/share.png"); }

@media (min-width: 768px) {
  .controls {
    order: -1;
    margin-bottom: 20px; }
  .controls__list {
    width: 100%;
    display: flex;
    justify-content: space-between; }
  .controls__item {
    width: 48%; }
    .controls__item--favorite {
      margin: 0; }
  .controls__link {
    font-size: 13px;
    margin: 0;
    padding: 8px 0; }
  .controls__button {
    font-size: 13px;
    padding: 8px 0; }
    .controls__button--share {
      display: none; } }

@media (min-width: 1000px) {
  .controls {
    margin-bottom: 40px; }
  .controls__list {
    justify-content: flex-start; }
  .controls__item {
    width: 39.7%; }
    .controls__item--shop {
      margin-right: 6%; } }

@media (min-width: 1370px) {
  .controls__item {
    width: 48%; }
    .controls__item--shop {
      margin-right: auto; } }

.social {
  display: none; }

@media (min-width: 768px) {
  .social__to-share {
    margin: 0;
    display: none; }
  .social {
    display: flex;
    align-items: center; }
  .social__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap; }
  .social__item {
    display: flex; }
    .social__item:last-child .social__link {
      margin-right: 15px; }
  .social__link {
    display: block;
    width: 30px;
    height: 30px;
    border: 2px solid #d9d9d9;
    border-radius: 50%;
    margin-right: 10px;
    position: relative; }
    .social__link::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 9px; }
    .social__link--vkontakte::after {
      width: 7px;
      height: 10px;
      background-image: url("../img/icons/vk.png"); }
    .social__link--google-plus::after {
      width: 13px;
      height: 11px;
      background-image: url("../img/icons/google-plus.png"); }
    .social__link--facebook::after {
      width: 5px;
      height: 12px;
      background-image: url("../img/icons/facebook.png"); }
    .social__link--twitter::after {
      width: 11px;
      height: 10px;
      background-image: url("../img/icons/twitter.png"); }
  .social__shared {
    background-color: #efefef;
    padding: 5px 10px;
    margin: 0;
    font-size: 13px;
    position: relative; }
    .social__shared::after {
      content: '';
      position: absolute;
      top: 12px;
      left: -4px;
      background-color: #efefef;
      width: 10px;
      height: 10px;
      transform: rotate(45deg); } }

@media (min-width: 1000px) {
  .social__to-share {
    display: block;
    font-size: 13px;
    text-transform: uppercase;
    margin-right: 20px; } }
